export const getReinsurance = ({ id, contract, insurer }) => {
  return new Promise((resolve, reject) => {
    axios
      .get(
        `/contract/reinsurance/${id}?contract=${contract}&insurer=${insurer}`
      )
      .then(
        (res) => {
          resolve(res.data);
        },
        (error) => {
          reject(error);
        }
      );
  });
};

